@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
*{margin:0;font-family:"Poppins", sans-serif;box-sizing:border-box}

.Loader{display:flex;width:100%;justify-content:center;align-items:center}.Loader__content{border-radius:50%;width:50px;height:50px;margin:20px auto;border:8px solid #ddd;border-left-color:green;animation:load8 1.2s infinite linear}@keyframes load8{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.container-feedline{margin:0;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:40px;gap:40px}.container-feedline__buttons{display:flex;grid-gap:4px;gap:4px}.container-feedline__error{display:block;width:360px;height:180px;display:flex;flex-direction:column;justify-content:space-around;align-items:center;border:2px solid red;border-radius:4px;text-align:center}.container-feedline__error-text{display:block}.feed-container{display:flex;width:480px}@media (max-width: 640px){.feed-container{width:360px}}.feed-container__avatar{flex-grow:0;display:inline-block;position:relative;width:56px;height:56px;overflow:hidden;border-radius:50%}@media (max-width: 640px){.feed-container__avatar{width:36px;height:36px}}.feed-container__avatar:hover{cursor:pointer}.feed-container__body{width:336px;margin-left:12px;display:flex;flex-direction:column;flex-grow:1;word-wrap:break-word}@media (max-width: 640px){.feed-container__body{width:207px}}@media (max-width: 640px){.feed-content{font-size:14px}}.feed-content__link{text-decoration:none;color:#161823}.feed-content__link:visited{text-decoration:none;color:#161823}.feed-content__name{margin:4px 0 0}.feed-content__name--main{color:#161823;font-size:18px;font-weight:700;margin-right:6px}@media (max-width: 640px){.feed-content__name--main{font-size:16px}}.feed-content__name--sub{font-size:14px}@media (max-width: 640px){.feed-content__name--sub{font-size:12px}}.feed-content__name:hover{cursor:pointer}.feed-content__video{height:600px;width:336px;margin-top:6px;object-fit:contain;border-radius:8px;overflow:hidden}@media screen and (max-width: 640px){.feed-content__video{height:370px;width:207px}}.feed-content__hashtags{font-weight:600}.feed-content__hashtags--hashtag{margin-right:4px}.feed-content__hashtags--hashtag:hover{text-decoration:underline;cursor:pointer}.pagination-button{all:none;display:inline-block;width:36px;border-radius:4px;border:2px solid #02608f;background-color:white;transition:0.25s}.pagination-button:hover{cursor:pointer;background-color:#91c4dd}.pagination-button--active{border-color:#02608f;background-color:#5188a3}.pagination-button--active:hover{background-color:#5188a3}.error-button{all:none;display:inline-block;width:140px;border-radius:4px;border:2px solid #bd1515;background-color:white;transition:0.25s;color:black}.error-button:hover{cursor:pointer;background-color:#f79d96}

.user-container{margin:0;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:40px;gap:40px}.user-container__user-info{margin-top:20px;display:flex;justify-content:center;align-items:center;width:100%}.user-container__user-feed{display:flex;flex-wrap:wrap;grid-gap:4px;gap:4px;width:640px}@media (max-width: 640px){.user-container__user-feed{flex-direction:column;justify-content:center;align-items:center;width:360px}}.user-container__back-button{text-decoration:none;display:inline-block;text-align:center;width:150px;height:30px;border:2px solid #02608f;background-color:white;transition:0.25s;border-radius:4px;color:black}.user-container__back-button:hover{background-color:#c0d7e2;color:black}.user-container__back-button:visited{text-decoration:none;color:black}.user-card{display:flex}.user-card__avatar{flex-grow:0;display:inline-block;position:relative;width:220px;height:220px;overflow:hidden;border-radius:50%}@media (max-width: 640px){.user-card__avatar{width:60px;height:60px}}.user-card__info{width:336px;margin-left:12px;display:flex;flex-direction:column;flex-grow:1;word-wrap:break-word}@media (max-width: 640px){.user-card__info{width:207px}}

.App{max-width:1200px;margin:0 auto;padding:48px;display:flex;justify-content:center;flex-direction:column}@media screen and (max-width: 640px){.App{max-width:360px;padding:4px}}

